<template>
  <div class="container">
    <el-form
      :model="regInfo"
      :rules="regRules"
      ref="ruleForm"
      label-width="100px"
      status-icon
      class="demo-ruleForm"
    >
    <el-form-item label="学号" prop="stuId">
        <el-input v-model="regInfo.stuId" placeholder="请输入学号"></el-input>
      </el-form-item>
      <el-form-item label="安全邮箱" prop="email">
        <el-input v-model="regInfo.email" placeholder="请输入邮箱"></el-input>
      </el-form-item>
      <el-form-item label="新密码" prop="newPassWord">
        <el-input v-model="regInfo.passWord" placeholder="密码"></el-input>
      </el-form-item>
      <el-form-item label="确认密码" prop="reNewPassWord">
        <el-input v-model="regInfo.rePassWord" placeholder="再次输入密码"></el-input>
      </el-form-item>
      <el-row>
        <el-col :span="16">
          <el-form-item label="邮箱验证码" prop="emailCode">
            <el-input v-model.number="regInfo.emailCode" placeholder="验证码"></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="5" :offset="3">
          <el-button @click="handleEamilCode()">获取</el-button>
        </el-col>
      </el-row>
      <el-form-item label="性别">
        <el-radio v-model="regInfo.sex" label="0">女</el-radio>
        <el-radio v-model="regInfo.sex" label="1">男</el-radio>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    var validatePass2 = (rule, rePassWord, callback) => {
      // 校验确认密码
      if (rePassWord === "") {
        callback(new Error("请再次输入密码"));
      } else if (rePassWord !== this.regInfo.passWord) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    var checkEmail = (rule, email, callback) => {
      // 校验邮箱
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
      if (!email) {
        return callback(new Error("邮箱不能为空"));
      }
      setTimeout(() => {
        if (mailReg.test(email)) {
          callback();
        } else {
          callback(new Error("请输入正确的邮箱格式"));
        }
      }, 100);
    };
    var validateemailCode = (rule, emailCode, callback) => {
      // 校验验证码
      if (!emailCode) {
        callback(new Error("请输入验证码"));
      } else if (emailCode.toString().length !== 4) {
        callback(new Error("请输入4位验证码!"));
      } else {
        callback();
      }
    };
    return {
      regInfo: {
          stuId: '',
        email: "",
        passWord: "",
        rePassWord: "",
        emailCode: null,
        isReg: 1
      },
      regRules: {
        email: [{ validator: checkEmail, trigger: "blur" }],
        passWord: [
          { required: true, message: "请输入新密码", trigger: "blur" }
        ],
        rePassWord: [{ validator: validatePass2, trigger: "blur" }],
        emailCode: [
          { validator: validateemailCode, trigger: "blur" },
          { type: "number", message: "验证码必须是数字" }
        ]
      }
    };
  },
  methods: {
    handleEamilCode() {
      console.log("handleEamilCode");
      //邮箱格式验证未做
    }
  }
};
</script>

